.project-container-father {
    width:100%;
    display:flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 130px;
}

.box{
    margin-bottom: 150px;
   
}

#aboutme{
    position:absolute;
    top:10px;
}

#projects{
    position:absolute;
    top:1500px;
}

#skills{
    position:absolute;
    top:630px;
}

#contact{
    position:absolute;
    top:5600px;
}

.section-title{
    font-size:55px;
    font-weight: 300;
    margin-bottom: 100px;
    /* margin-left: -1000px; */
}

@media only screen and (max-width: 1024px) {
  
    /* .vector1{
        height:1000px;
        /* min-width: 370px; 
    } */
    .project-container-father{
        margin-top: 1700px;
    }

    .box{
        width:auto;
    }

    .section-title{
        font-size:60px;
        text-align:center;
        margin-left: 0px;
        
    }

 
    
    #projects{
        top:1500px;
    }
    
    #skills{
        top:870px;
    }
    
    #contact{
        top:8100px !important;
    }
  }

  .footer{
      text-align:center;
      margin-bottom:20px;
  }



  @media only screen and (max-width: 1200px) {
    .section-title{
        font-size: 39px;
        margin-bottom: 80px;
        margin-top: 0px;
    }

    .box{
        margin-bottom: 90px;
    }

    .project-container-father{
        margin-top: 10px;
    }

    #contact{
       
        top:5300px;
    }
  }

  @media only screen and (max-width: 690px){
    #aboutme{
        position:absolute;
        top:10px;
    }
    
    #projects{
        position:absolute;
        top:1600px;
    }
    
    #skills{
        top:1000px;
    }
    
    #contact{
        position:absolute;
        top:5850px !important; 
    }
  }